import React from 'react';
import { withStyles } from '@material-ui/core/styles';


const styles = theme => ({
     root:{
        marginTop:60,
     },
  });

class Home extends React.Component{

    render(){
        const{classes} = this.props;
        return(
            <div>This is the home page</div>
        );
    }
}

export default withStyles(styles)(Home);